export default "<template>\r\n  <div>\r\n    <el-row :gutter=\"20\">\r\n      <el-col :span=\"12\">\r\n        <section style=\"height:400px;background-color:#fff\">\r\n          <nm-scrollbar>\r\n            <p v-for=\"i in 100\" :key=\"i\">第{{i}}个</p>\r\n          </nm-scrollbar>\r\n        </section>\r\n      </el-col>\r\n      <el-col :span=\"12\">\r\n        <section style=\"height:400px;background-color:#fff\">\r\n          <nm-scrollbar horizontal>\r\n            <p v-for=\"i in 100\" :key=\"i\">\r\n              <span v-for=\"m in i\" :key=\"i+''+m\">hahaah</span>\r\n            </p>\r\n          </nm-scrollbar>\r\n        </section>\r\n      </el-col>\r\n    </el-row>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  data () {\r\n    return {\r\n      split: 0.3,\r\n      listbox: [],\r\n      list: {\r\n        title: '用户列表',\r\n        icon: 'list',\r\n        action: this.query,\r\n        model: {\r\n          name: ''\r\n        },\r\n        cols: [{\r\n          name: 'id',\r\n          label: '编号',\r\n          show: false\r\n        },\r\n        {\r\n          name: 'name',\r\n          label: '姓名'\r\n        },\r\n        {\r\n          name: 'age',\r\n          label: '年龄'\r\n        }\r\n        ]\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    query () {\r\n      const rows = [{ id: 1, name: '张三', age: 22 }, { id: 2, name: '李四', age: 26 }, { id: 3, name: '王五', age: 26 }]\r\n      return new Promise(resolve => {\r\n        resolve({\r\n          rows,\r\n          total: 3\r\n        })\r\n      })\r\n    },\r\n    onSelect (row) {\r\n      this.listbox.push({\r\n        label: row.name,\r\n        value: row.id\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n"